    </el-tabs>
</template>

<script>
export default {
    name: "PvpTabs",
    data() {
        return {
            view: "index",
        };
    },
    watch: {
        $route: {
            handler(_route) {
                this.view = _route.name;
            },
            immediate: true,
            deep: true,
        },
        view: {
            handler() {
                const query =
                    this.view === "sandbox"
                        ? {}
                        : {
                              subtype: this.$route.query.subtype,
                          };
                this.$router.push({
                    name: this.view,
                    query,
                });
            },
            immediate: true,
        },
    },
};
</script>

